import { useEffect } from "react";
import { useState } from "react"
import { getDataFromPokemon } from "../service/pokeapi";

import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea, Grid } from '@mui/material';
import { Cardcomponent } from "../component/Cardcomponent";
import { Modalcomponent } from "../component/Modalcomponent";

const Home=(valor)=>{

    //variable que guarda la lista
    const [pokemones, setPokemon] = useState([]);
    const [Siguiente, setSiguiente] = useState("");
    const [Atras, setBack] = useState([]);


    const fetchPokemonList = async (dato) =>{
        const listPokemones = await getDataFromPokemon(dato);
        
        console.log("listPokemones.result", listPokemones.results);
        setPokemon(listPokemones.results);
        setSiguiente(listPokemones.next); 
        setBack(listPokemones.previous)
        };
    //funcion para ejecutar la funcion de la api
    //aca llamamos la funcion que deseamos ejecutar
    //TODO: useEffect se coloca un array vacio para evitar un bucle infinito
    //la funcion que esta dentro del usseefect se llama n veces de forma infinita
    useEffect(()=>{
      /*   getDataFromPokemon().then((data)=>{setPokemon(data.results);
            setSiguiente(data.next); setBack(data.previous)}); */   fetchPokemonList();  },[])
           
    const next=()=>
    {
        fetchPokemonList(Siguiente);
    }
    const atrass=()=>
    {
        fetchPokemonList(Atras);
    }
    console.log(pokemones);
    return(
        <>
        <h1>pokedex</h1>
        <Grid container  justifyContent="center"
        alignItems="center" gap={2}>
        {
            pokemones.map((info,index)=>{
                return <div key={index}>

                    <Grid  md={12} lg={12} sm={12} xs={12}>
                        <Card  sx={{ maxWidth: 300 }}>
                        <CardActionArea>
                            <Cardcomponent nombre={info.name} url={info.url}/>
                            <CardContent>
                            <Typography gutterBottom variant="h4" component="div">
                                {info.name}
                            </Typography>
                            <Typography variant="body2" color="text.secondary">
                                <Modalcomponent nombre={info.name} url={info.url}/>
                            </Typography>
                            </CardContent>
                        </CardActionArea>
                        </Card>
                    </Grid>
                   


                    </div>
             
            })
        }
         <div className="">
            <div>
                <button className="btn btn-primary m-3" onClick={atrass}>Anterior</button>
                <button className="btn btn-primary m-3" onClick={next}>Siguiente</button>
            </div>
               
            </div>
           </Grid>


           
        </>
    )
}

export default Home;